import * as React from "react";
import { graphql, PageProps } from "gatsby";
import Img from "gatsby-image";
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";

const ContactPage: React.FC<PageProps> = (props) => {
  const data = props.data as any;
  const backgroundImage = data.backgroundImage;

  const onSubmit = async (e: any) => {
    e.preventDefault();

    try {
      const { name, email, message } = e.target;
      const [nameValue, emailValue, messageValue] = [
        name.value,
        email.value,
        message.value,
      ];

      const enquireForValues = Array.from(e.target.enquireFor)
        .map((x: any) => ({
          value: x.value,
          checked: x.checked,
        }))
        .filter((x) => x.checked)
        .map((x) => x.value)
        .join(",");

      console.log(nameValue, emailValue, messageValue, enquireForValues);

      window.location.href = "/";
    } catch (err) {}
  };

  return (
    <main>
      <div className="relative">
        <Img
          fluid={backgroundImage.childImageSharp.fluid}
          alt={backgroundImage.name}
          className="!absolute z-[-1] w-full h-full !object-cover !object-center"
        />
        <div className="blur-background">
          <Navbar />
        </div>
        <form onSubmit={onSubmit}>
          <div
            className="py-16 bg-white text-[#262A2D] container select-none"
            id="services"
          >
            <h2 className="text-2xl">Contact Us</h2>
            <div className="mb-10"></div>
            <span className="block mb-3">Full Name</span>
            <input
              className="border p-3 w-full lg:max-w-[50%]"
              type="text"
              name="name"
              required
            />
            <div className="mb-4"></div>
            <span className="block mb-3">Email</span>
            <input
              className="border p-3 w-full lg:max-w-[50%]"
              type="email"
              name="email"
              required
            />
            <div className="mb-4"></div>
            <span className="block mb-3">Message</span>
            <textarea
              className="border p-3 w-full lg:max-w-[50%]"
              name="message"
              rows={4}
              required
            />
            <div className="mb-4"></div>
            <span className="block mb-3">Enquire for</span>
            <label className="block pt-2">
              <input
                type="checkbox"
                name="enquireFor"
                id="equipment"
                value="Equipment"
              />
              Equipment
            </label>
            <label className="block pt-2">
              <input
                type="checkbox"
                name="enquireFor"
                id="rawMaterials"
                value="Raw-Materials"
              />
              Raw Materials
            </label>
            <label className="block pt-2">
              <input
                type="checkbox"
                name="enquireFor"
                id="maintenance"
                value="Maintenance"
              />
              Maintenance
            </label>
            <div className="mb-10"></div>
            <button
              type="submit"
              className="bg-[#262A2D] text-white px-6 py-2 rounded-full"
            >
              Submit
            </button>
          </div>
        </form>
      </div>
      <Footer />
    </main>
  );
};

export default ContactPage;

export const query = graphql`
  query {
    backgroundImage: file(relativePath: { eq: "solar-farm.jpg" }) {
      name
      childImageSharp {
        fluid {
          ...GatsbyImageSharpFluid
        }
      }
    }
  }
`;
